import React from "react";
import RPR_HOMES from "../components/slider/home/rpr.png";
const Footer = () => {
  return (
    <>
      <div
        style={{ overflow: "hidden" }}
        className="footer d-flex flex-column align-items-center text-center justify-content-center"
      >
        <img
          data-aos="flip-right"
          style={{ width: "80px" }}
          src={RPR_HOMES}
          alt="RBV Corporate"
        />
        <h3 data-aos="zoom-in-up" className="m-3">
          Raipur Homes
        </h3>
        <div className="m-3 text-info" style={{ fontSize: "12px" }}>
          Disclaimer: The Imagery Used In The Brochure Is Indicative Of Style
          Only. The Photographs Of The Interiors, Surrounding Views And Location
          May Have Been Digitally Enhanced Or Altered And Do Not Represent
          Actual Or Surrounding Views. Standard Fittings And Finishes Are
          Subject To Availability And Vendor Discretion. The Images Shown In The
          Brochure Are Not Standard And Will Not Be Provided As A Part Of An
          Apartment. The Information Contained Herein Is Believed To Be True But
          Not Guaranteed. The Colours Of The Buildings Are Indicative. This Is A
          Reference Document Intended Only To Provide Generic Information And
          Does Not Constitute An Offer Or Contract.
        </div>
        <p className="border-none m-auto text-gray">
          Copyright © www.raipurhomes.com 2023 All rights reserved.
        </p>
      </div>

      <button
        style={{
          position: "fixed",
          bottom: "10px",
          right: "10px",
          cursor: "pointer",
          padding: "5px",
          borderRadius: "5px",
          outline: "none",
          backgroundColor: "#00214d",
          zIndex: "99",
        }}
        className="btn-color-rpr d-none"
      >
        I am Interested
      </button>
    </>
  );
};

export default Footer;
