import React, { useEffect } from "react";
import { ContactSlider } from "./slider/BasicSlider";
import { docTitle } from "./docTitle";
import Aos from "aos";
import "aos/dist/aos.css";

const Contact = () => {
  docTitle("Contact Us - Raipur Homes");

  useEffect(() => {
    Aos.init({
      duration: 1500,
    });
  }, []);
  return (
    <>
      <ContactSlider />
      <div data-aos="fade-up" className="about-d-flex">
        <h1 data-aos="fade-right" className="mb-3">
          Contact With Raipur Homes
        </h1>

        <p data-aos="fade-left">
          <span>
            Mobile : +91 8882634463 <br />
            E-Mail : support@raipurhomes.com <br />
          </span>
          Well, real estate is more than buying and selling a house. This
          million-dollar industry has thousands of such facts that may pique
          your interest. Whether you’re a seasoned investor or a first-time
          homebuyer, you’ll find the following 20 real estate facts interesting.
        </p>
        <p data-aos="fade-up">
          In 2001, 69% of home sellers relied on agents to sell their homes.
          However, in 2023 almost 89% preferred real estate agents. The primary
          reason is to avoid the hassle and time-consuming process of getting a
          property sold. While 7% sold through FSBO, and less than 1% sold via
          iBuyer.
        </p>
      </div>
    </>
  );
};

export default Contact;
