import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { docTitle } from "../docTitle";
import { BASE_URL } from "../helper/helper";

const ListAdmins = () => {
  docTitle("Users - Get your best service with RBVCorporate...");

  const [admins, setAdmins] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    if (localStorage.getItem("type") !== "ADMIN") {
      navigate("/");
    }
  });

  useEffect(() => {
    axios
      .get(`${BASE_URL}/admin/admins`)
      .then((res) => {
        // console.log(res.data);
        setAdmins(res.data.data);
      })
      .catch((err) => {
        alert("admin list getting err");
      });
  }, []);

  return (
    <>
      <div className="admin-card">
        <div className="admin-flex">
          <h1>This is the List of Admins Section</h1>

          <div className="admin-dash-list">
            <Link to="/admin/addadmin">
              <div className="thumbLogo">&#9758;</div> ADD ADMIN
            </Link>
            <Link to="/admin/dashadmin">
              <div className="thumbLogo">&#9758;</div> GO ADMIN DASHBOARD
            </Link>
          </div>

          {admins.length > 0
            ? admins.map((adminItem, adminIndex) => {
                return (
                  <div key={adminIndex} className="admlist-flex">
                    <div className="admlist-card">{adminItem?.userName}</div>

                    <div className="admlist-card">{adminItem?.password}</div>

                    <div className="admlist-card">{adminItem?.type}</div>

                    <div className="admlist-card">{adminItem?.status}</div>

                    <div className="admlist-card">{adminItem?.date}</div>
                  </div>
                );
              })
            : "Please create users...."}
        </div>
      </div>
    </>
  );
};

export default ListAdmins;
