import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { docTitle } from "../docTitle";
import { BASE_URL } from "../helper/helper";

const GetEditProducts = (props) => {
  docTitle("Update - Get your best service with RBVCorporate...");

  // const [images, setImages] = useState("");
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [amount, setAmount] = useState("");
  // const [servicetype, setServicetype] = useState("");
  const [seller, setSeller] = useState("");

  const params = useParams();
  // console.log(params, "11");
  useEffect(() => {
    const viewdata = () => {
      try {
        const id = params.id;

        axios.get(`${BASE_URL}/api/get-products/${id}`).then((res) => {
          // console.log("get page success for edit", res.data.data, 30);

          // setImages(res.data.data.imageFile);
          setTitle(res.data.data.title);
          setDescription(res.data.data.description);
          setAmount(res.data.data.amount);
          // setServicetype(res.data.data.servicetype);
          setSeller(res.data.data.seller);
        });
      } catch (err) {
        alert("get edit page error");
      }
    };
    viewdata();
    // eslint-disable-next-line
  }, []);

  const handleUpdate = () => {
    console.log({
      id: params.id,
      // imageFile: images,
      title,
      amount,
      // servicetype,
      seller,
      description,
    });

    const inputData = {
      id: params.id,
      // imageFile: images,
      title,
      amount,
      // servicetype,
      seller,
      description,
    };

    // console.log(images, title, amount:Number(amount), servicetype, seller, description, 74);

    // THIS URL FOR OUTPUT AFTER ADD PRODUCT DATA, AXIOS FUNCTION RUN WHEN CLICK USER ON UPDATE BUTTON after change and set on output file, End Tutorials, Now i'll go "GetProduct.js" file with point 11
    axios
      .post(`${BASE_URL}/api/edit-product`, inputData)
      .then((res) => {
        // console.log("product updated success", res);
        alert("product updated success");
        props.showAlert("Service Updated Successfully", "success");
      })
      .catch((error) => {
        alert("product not updated getting error");
      });
  };

  return (
    <>
      <div className="admin-card">
        <div className="add-services">
          <div className="admin-flex">
            <h3
              style={{
                margin: "auto",
                alignItems: "center",
                justifyContent: "center",
                display: "flex",
              }}
            >
              Edit Services
            </h3>
            {/* <Link to="/add/product">&#9758; Go To Add Services</Link>
            <br /> <Link to="/get/product">&#9758; Show Adding Product</Link> */}
            {/* Image : <div style={{ display: "flex" }}> */}
            {/* <input
            className=" input-file"
            type="file"
            onChange={(e) => setImages(e.target.files[0])}
          /> */}
            {/* <img
              width="10%"
              height="50px"
              src={`http://localhost:6001/${images.imageFile}`}
              alt="image not upload"
            />
          </div> */}
            <div className="input-amount">
              <label className="label-price" htmlFor="label-price">
                Price :{" "}
              </label>
              <span>
                <input
                  className="input-admin"
                  type="text"
                  defaultValue={amount}
                  onChange={(e) => setAmount(e.target.value)}
                />
              </span>{" "}
            </div>
            <div className="input-title">
              <label className="title-label" htmlFor="title-label">
                Name :{" "}
              </label>
              <input
                className="input-admin"
                type="text"
                defaultValue={title}
                onChange={(e) => setTitle(e.target.value)}
              />
            </div>
            {/* Select :{" "}
          <select
            className="input-field select-field"
            defaultValue={servicetype}
            onChange={(e) => setServicetype(e.target.value)}
            name="serviceType"
            id="serviceType"
          >
            <option value="ZARA">ZARA</option>
            <option value="ADIDAS">ADIDAS</option>
            <option value="PUMA">PUMA</option>
            <option value="NIKE">NIKE</option>
          </select> */}
            <div className="input-seller">
              <label className="seller-label" htmlFor="seller-label">
                Seller :
              </label>
              <input
                className="input-admin"
                type="text"
                defaultValue={seller}
                onChange={(e) => setSeller(e.target.value)}
              />{" "}
            </div>

            <div className="input-details">
              <label className="label-details" htmlFor="label-details">
                Details :
              </label>

              <input
                className="input-admin"
                type="text"
                defaultValue={description}
                onChange={(e) => setDescription(e.target.value)}
              />
            </div>
            <button
              className="submit-btn"
              onClick={handleUpdate}
              disabled={
                title.length < 5 ||
                description.length < 5 ||
                amount.length < 1 ||
                seller.length < 5
              }
            >
              Update
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default GetEditProducts;
