// THIS IS THE LOGIN ADMIN PAGES

import axios from "axios";
import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { docTitle } from "../docTitle";
import { BASE_URL } from "../helper/helper";

const LoginAdmin = (props) => {
  docTitle("Login - Get your best service with RBVCorporate...");

  const navigate = useNavigate();

  const [userName, setUsername] = useState("");
  const [password, setPassword] = useState("");

  const handleClick = (e) => {
    e.preventDefault();
    // console.log(userName, password);

    axios
      .post(`${BASE_URL}/admin/loginadmin`, {
        userName,
        password,
        date: new Date(),
      })
      .then((res) => {
        // console.log(res.data);

        localStorage.setItem("type", res.data.type);
        localStorage.setItem("token", res.data.token);

        if (localStorage.getItem("token")) {
          navigate("/");
          props.showAlert("ADMIN Logged in Successfully", "success");
        }
      })
      .catch((err) => {
        alert("Login Admin data getting error");
      });
  };

  return (
    <>
      <div className="admin-card">
        <div className="w-50 m-auto border p-3 rounded">
          <div className="addmin-flex">
            <h3 className="d-flex m-auto">Login Admin</h3>

            <form action="">
              <input
                value={userName}
                onChange={(e) => setUsername(e.target.value)}
                className="input-admin"
                type="text"
                autoComplete="username"
              />
              <input
                autoComplete="current-password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="input-admin"
                type="password"
                name="password"
              />

              <button
                disabled={userName.length === 0 || password.length === 0}
                onClick={handleClick}
                className="submit-btn"
              >
                SUBMIT
              </button>
              <Link to="/admin/addadmin">Create an Account?</Link>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default LoginAdmin;
