import React, { useEffect, useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";

// Social Media
import RPR_HOMES from "../components/slider/home/rpr.png";

const NavBar = (props) => {
  const [open, setOpen] = useState(false);

  const openNav = () => {
    setOpen(!open);
  };

  // HeighLight Navbar Menu
  let location = useLocation();
  useEffect(() => {
    // console.log(location.pathname);
  }, [location]);

  let navigate = useNavigate();

  const handleLogout = (e) => {
    e.preventDefault();
    localStorage.removeItem("token");
    localStorage.removeItem("type");

    navigate("/admin/loginadmin");
    alert("Are you sure want to Logout!");
    props.showAlert("User Logged out Successfully", "success");
  };

  useEffect(() => {
    if (!localStorage.getItem("token")) {
      navigate("/admin/loginadmin");
    }

    // eslint-disable-next-line
  }, []);

  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-white bg-white justify-content-between">
        <div className="nav-logo">
          <Link title="RBVCorporate" className="navbar-brand" to="/">
            <img
              style={{ width: "80px" }}
              src={RPR_HOMES}
              alt="RBV Corporate"
            />

            {/* AARSH PROJECT */}
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
        </div>

        {localStorage.getItem("token") && (
          <ul
            className={
              open ? "navbar-nav mr-auto collapseNav" : "navbar-nav mr-auto"
            }
          >
            <Link
              className={open ? "closebtn closebtnOpen" : "closebtnOpen"}
              onClick={openNav}
            >
              &times;
            </Link>

            <li className="nav-item active">
              <Link
                className={` nav-link ${
                  location.pathname === "/" ? "active" : ""
                }`}
                to="/"
              >
                Home
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className={` nav-link ${
                  location.pathname === "/" ? "active" : ""
                }`}
                to="/about"
              >
                About
              </Link>
            </li>

            <li className="nav-item">
              <Link
                className={` nav-link ${
                  location.pathname === "/" ? "active" : ""
                }`}
                to="/contact"
              >
                Contact Us
              </Link>
            </li>
          </ul>
        )}

        <div>
          {/* Logout function with token and type */}
          {!localStorage.getItem("token") ||
          localStorage.getItem("token") === "undefined" ||
          localStorage.getItem("token") === "" ? (
            <div
              className="collapse navbar-collapse"
              id="navbarSupportedContent"
            >
              <form className="d-flex">
                <Link
                  className="btn btn-color-rpr mx-1"
                  to="/admin/loginadmin"
                  role="button"
                >
                  Login
                </Link>
              </form>
            </div>
          ) : (
            <div
              className="collapse navbar-collapse"
              id="navbarSupportedContent"
            >
              <form className="logout-flex">
                <Link
                  onClick={handleLogout}
                  className="btn btn-primary mx-1"
                  to="/admin/loginadmin"
                  role="button"
                >
                  Logout
                </Link>
              </form>
            </div>
          )}
        </div>
      </nav>
    </>
  );
};

export default NavBar;
