// THIS IS THE CREATE USER PAGES
import axios from "axios";
import React, { useState } from "react";

import { Link, useNavigate } from "react-router-dom";
import { docTitle } from "../docTitle";
import { BASE_URL } from "../helper/helper";

const AddAdmins = (props) => {
  docTitle("Register - Raipur Homes...");
  const navigate = useNavigate();

  // useEffect(() => {
  //   if (localStorage.getItem("token")) {
  //     navigate("/admin/loginadmin");
  //   }
  // });

  const [userName, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [status, setStatus] = useState("");
  const [type, setType] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    // console.log(userName, password, status, type);
    axios
      .post(`${BASE_URL}/admin/admins`, {
        userName,
        password,
        status,
        type,
        date: new Date(),
      })
      .then((res) => {
        // console.log(res.data);
        navigate("/admin/loginadmin");
        props.showAlert("ADMIN Added Successfully", "success");
      })
      .catch((err) => {
        alert("adAdmin data add getting error", err);
      });
  };

  return (
    <>
      <div className="admin-card">
        <div className="w-50 m-auto border p-3 rounded">
          <div className="addmin-card">
            <h4>Create Admin/User Profile</h4>

            <div className="admin-dash-list d-none" style={{ float: "right" }}>
              <Link to="/admin/dashadmin">
                <div className="thumbLogo">&#9758;</div> GO ADMIN DASHBOARD
              </Link>
            </div>

            <form action="">
              <input
                value={userName}
                onChange={(e) => setUsername(e.target.value)}
                className="input-admin"
                type="text"
                placeholder="Enter Id or User name"
                autoComplete="username"
              />
              <input
                autoComplete="current-password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="input-admin"
                type="password"
                placeholder="Enter password"
              />
              {/* this is the Admin Status */}
              <select
                value={status}
                onChange={(e) => setStatus(e.target.value)}
                className="select-admin admin-status"
                name="admin-status"
                id="admin-status"
              >
                <option>Select Status</option>
                <option value="ACTIVE">ACTIVE</option>
                <option value="BLOCK">BLOCK</option>
                <option value="DELETE">DELETE</option>
              </select>

              {/* this is the Admin User Type */}
              <select
                value={type}
                onChange={(e) => setType(e.target.value)}
                className="select-admin admin-type"
                name="admin-type"
                id="admin-type"
              >
                <option>Select User</option>
                <option value="ADMIN">ADMIN</option>
                <option value="SUBADMIN">SUBADMIN</option>
                <option value="CUSTOMER">CUSTOMER</option>
              </select>

              <button
                disabled={
                  userName.length === 0 ||
                  password.length === 0 ||
                  status.length === 0 ||
                  type.length === 0
                }
                onClick={handleSubmit}
                className="submit-btn"
              >
                SUBMIT
              </button>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default AddAdmins;
