// give rafce and enter hit
import React, { useEffect, useState } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import { HomeSlider } from "./slider/BasicSlider";
import { Amenities } from "./slider/CloneSlider";
import { docTitle } from "./docTitle";
import { useNavigate } from "react-router-dom";

const imagePlan = require("../components/slider/home/imgplan.jpg");
const luxplan = require("../components/slider/home/luxplan.png");
const int1 = require("../components/slider/home/int1.avif");
const int2 = require("../components/slider/home/int2.jpg");
const int3 = require("../components/slider/home/int3.jpg");
const ext1 = require("../components/slider/home/ext1.webp");
const ext2 = require("../components/slider/home/ext2.webp");
const ext3 = require("../components/slider/home/ext.webp");
const amt1 = require("../components/slider/home/amt.avif");
const amt2 = require("../components/slider/home/amt2.jpg");
const amt3 = require("../components/slider/home/amt1.avif");

const Home = () => {
  docTitle("Raipur Homes - Your Dreams House...");
  const navigate = useNavigate();

  // handle admin tag
  const [handleAdminTag, setHandleAdminTag] = useState(1);
  const handleAdmin = (e) => {
    setHandleAdminTag(e);
  };

  // handle admin tag
  const [gallery, setGallery] = useState(1);
  const handleGallery = (e) => {
    setGallery(e);
  };

  useEffect(() => {
    if (!localStorage.getItem("token")) {
      navigate("/admin/loginadmin");
    }

    Aos.init({
      duration: 1500,
    });

    // eslint-disable-next-line
  }, []);
  return (
    <>
      <div style={{ overflow: "hidden" }}>
        {/* <div className="post-animate">
          <button className="position-absolute bottom-10 right-5 border shadow text-dark bg-blue">
            I am Interested
          </button>
        </div> */}
        <HomeSlider />
        <div className="home-CloneSlider" style={{ margin: "5%" }}>
          <h2 data-aos="fade-right" className="m-auto text-center">
            RAIPUR HOMES - LUXURY APARTMENTS, VILLA RAIPUR, CHHATTISGARH
          </h2>
          <p data-aos="fade-left" className="text-secondary mt-5 text-center">
            Embrace the cultural richness of South Raipur Chhattisgarh with
            Raipur Homes, where luxury living harmonizes with the city's
            artistic soul. Our premium 3 BHK Tri-deck Residences redefine modern
            living with elegance and sophistication among the vibrant enclave of
            Raipur Chhattisgarh Road. These meticulously crafted flats in Raipur
            Chhattisgarh Road offer the perfect blend of comfort, convenience,
            and style. Nestled in a serene locale, Raipur Homes presents a world
            of amenities and lush greenery, making it the ideal choice for those
            seeking luxury apartments in Raipur Chhattisgarh Road. With spacious
            interiors and top-notch finishes, your permanent address provides a
            haven of relaxation and contemporary living.
          </p>
        </div>

        <div data-aos="fade-up" className="project-config">
          <div className="p-5 m-5">
            <div data-aos="fade-up-right" className="project-head">
              <h1 className="m-auto d-flex align-item-center justify-content-center text-white">
                PROJECT CONFIGURATION
              </h1>
              <div className="d-flex align-item-center justify-content-center m-3">
                <button
                  onClick={() => handleAdmin(1)}
                  style={{ cursor: "pointer" }}
                  type="button"
                  className="m-1 btn-color-rpr  p-1 rounded outline-0"
                >
                  3 BHK GRAND
                </button>
                <button
                  onClick={() => handleAdmin(2)}
                  style={{ cursor: "pointer" }}
                  type="button"
                  className="m-1 btn-color-rpr  p-1 rounded outline-0"
                >
                  3 BHK LUXURY
                </button>
              </div>
            </div>

            {handleAdminTag === 1 && (
              <div
                data-aos="fade-up-left"
                className="project-data d-flex align-items-center justify-content-center w-100"
              >
                <div data-aos="fade-right" className="project-plan m-5">
                  <img
                    style={{ width: "550px", height: "300px" }}
                    src={imagePlan}
                    alt="Plan"
                  />
                </div>

                <div
                  data-aos="fade-left"
                  className="plan-details border p-3 w-25 rounded text-white  shadow-2"
                >
                  <h4>MASTER PLAN</h4>
                  <b>Configuration Type</b>
                  <p>3 BHK</p>
                  <hr />
                  <b>Super Built-up Area</b>
                  <p>1744 SFT</p>
                  <hr />

                  <button
                    style={{ cursor: "pointer" }}
                    type="button"
                    className="btn-color-rpr p-1 rounded outline-0"
                  >
                    CHECK PRICE
                  </button>
                </div>
              </div>
            )}

            {handleAdminTag === 2 && (
              <div
                data-aos="fade-up-left"
                className="project-data d-flex align-items-center justify-content-center w-100"
              >
                <div data-aos="fade-right" className="project-plan m-5">
                  <img
                    style={{ width: "550px", height: "300px" }}
                    src={luxplan}
                    alt="Plan"
                  />
                </div>

                <div
                  data-aos="fade-left"
                  className="plan-details border p-3 w-25 rounded text-white  shadow-2"
                >
                  <h4>MASTER PLAN</h4>
                  <b>Configuration Type</b>
                  <p>3 BHK</p>
                  <hr />
                  <b>Super Built-up Area</b>
                  <p>1870 SFT - 1937 SFT</p>
                  <hr />

                  <button
                    style={{ cursor: "pointer" }}
                    type="button"
                    className="btn-color-rpr p-1 rounded outline-0"
                  >
                    CHECK PRICE
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>

        <div data-aos="fade-up" className="gallery bg-light">
          <div data-aos="flip-left" className="project-head">
            <h2 className="m-auto d-flex align-item-center justify-content-center">
              GALLERY
            </h2>
            <div className="d-flex align-item-center justify-content-center m-3">
              <button
                onClick={() => handleGallery(1)}
                style={{ cursor: "pointer" }}
                type="button"
                className="m-1 btn-color-rpr  p-1 rounded outline-0"
              >
                INTERIOR
              </button>

              <button
                onClick={() => handleGallery(2)}
                style={{ cursor: "pointer" }}
                type="button"
                className="m-1 btn-color-rpr  p-1 rounded outline-0"
              >
                EXTERIOR
              </button>
              <button
                onClick={() => handleGallery(3)}
                style={{ cursor: "pointer" }}
                type="button"
                className="m-1 btn-color-rpr  p-1 rounded outline-0"
              >
                AMENITIES
              </button>
              <button
                onClick={() => handleGallery(4)}
                style={{ cursor: "pointer" }}
                type="button"
                className="m-1 btn-color-rpr  p-1 rounded outline-0"
              >
                TESTIMONIALS
              </button>
            </div>
          </div>

          <div className="gallery-data">
            {gallery === 1 && (
              <div className="interior-part d-flex align-items-center justify-content-center m-auto">
                <div data-aos="flip-right" className="img-container">
                  <img
                    className="m-1 hover-img"
                    style={{ width: "400px", height: "300px" }}
                    src={int1}
                    alt="Interior Plan"
                  />
                </div>
                <div data-aos="flip-right" className="img-container">
                  <img
                    className="m-1 hover-img"
                    style={{ width: "400px", height: "300px" }}
                    src={int2}
                    alt="Interior Plan"
                  />
                </div>
                <div data-aos="flip-right" className="img-container">
                  <img
                    className="m-1 hover-img"
                    style={{ width: "400px", height: "300px" }}
                    src={int3}
                    alt="Interior Plan"
                  />
                </div>
              </div>
            )}

            {gallery === 2 && (
              <div className="interior-part d-flex align-items-center justify-content-center m-auto">
                <div data-aos="flip-right" className="img-container">
                  <img
                    className="m-1 hover-img"
                    style={{ width: "400px", height: "300px" }}
                    src={ext1}
                    alt="Interior Plan"
                  />
                </div>

                <div data-aos="flip-right" className="img-container">
                  <img
                    className="m-1 hover-img"
                    style={{ width: "400px", height: "300px" }}
                    src={ext2}
                    alt="Interior Plan"
                  />
                </div>
                <div data-aos="flip-right" className="img-container">
                  <img
                    className="m-1 hover-img"
                    style={{ width: "400px", height: "300px" }}
                    src={ext3}
                    alt="Interior Plan"
                  />
                </div>
              </div>
            )}

            {gallery === 3 && (
              <div className="interior-part d-flex align-items-center justify-content-center m-auto">
                <div data-aos="flip-right" className="img-container">
                  <img
                    className="m-1 hover-img"
                    style={{ width: "400px", height: "300px" }}
                    src={amt1}
                    alt="Interior Plan"
                  />
                </div>

                <div data-aos="flip-right" className="img-container">
                  <img
                    className="m-1 hover-img"
                    style={{ width: "400px", height: "300px" }}
                    src={amt2}
                    alt="Interior Plan"
                  />
                </div>
                <div data-aos="flip-right" className="img-container">
                  <img
                    className="m-1 hover-img"
                    style={{ width: "400px", height: "300px" }}
                    src={amt3}
                    alt="Interior Plan"
                  />
                </div>
              </div>
            )}

            {gallery === 4 && (
              <div className="interior-part d-flex align-items-center justify-content-center m-auto">
                <div className="m-2" data-aos="flip-up">
                  <iframe
                    width="420"
                    height="315"
                    src="https://www.youtube.com/embed/2fD5T1R1uG0"
                    title='Raipur Homes - "Don&#39;t wait to buy real estate. Buy real estate and wait."'
                    frameborder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                    referrerpolicy="strict-origin-when-cross-origin"
                    allowfullscreen
                  ></iframe>
                </div>
                <div className="m-2" data-aos="flip-up">
                  <iframe
                    width="420"
                    height="315"
                    src="https://www.youtube.com/embed/hxvgA-o8bCk"
                    title="Raipur Homes Presents Rental Properties in Your Area. Homes, Offices etc. #raipurhomes #raipurian"
                    frameborder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                    referrerpolicy="strict-origin-when-cross-origin"
                    allowfullscreen
                  ></iframe>
                </div>

                <div className="m-2" data-aos="flip-up">
                  <iframe
                    width="420"
                    height="315"
                    src="https://www.youtube.com/embed/-3uvC1-mtoQ"
                    title="Luxurious Fully Furnished Houses and Flats for Sale in Raipur. #raipurhomes #flatsforsale #raipur"
                    frameborder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                    referrerpolicy="strict-origin-when-cross-origin"
                    allowfullscreen
                  ></iframe>
                </div>
              </div>
            )}
          </div>
        </div>

        <div data-aos="zoom-in-down" className="mt-5 mb-3">
          <Amenities />
        </div>
      </div>
    </>
  );
};

export default Home;
