import "./App.css";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import NavBar from "./components/NavBar";
import Home from "./components/Home";
import About from "./components/About";
import Contact from "./components/Contact";
import Footer from "./components/Footer";
import ServicesAdmin from "./components/admin/ServicesAdmin";
import ListAdmins from "./components/admin/ListAdmins";
import AddAdmins from "./components/admin/AddAdmins";
import LoginAdmin from "./components/admin/LoginAdmin";
import AdminDash from "./components/admin/AdminDash";

import { Provider } from "react-redux";
import store from "./store";

// Show the Alert message
import Alert from "./components/Alert";
import { useState } from "react";
import GetEditProducts from "./components/admin/GetEditProducts";
import Swiper from "./components/slider/Swiper";

function App() {
  const [alert, setAlert] = useState(null);
  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type,
    });
    setTimeout(() => {
      setAlert(null);
    }, 3000);
  };

  return (
    <>
      <Provider store={store}>
        <Router>
          <div className="home-container">
            <NavBar showAlert={showAlert} />
            <Alert alert={alert} />
            <Routes>
              <Route exact path="/" element={<Home />} />
              <Route
                exact
                path="/admin/addadmin"
                element={<AddAdmins showAlert={showAlert} />}
              />

              <Route
                exact
                path="/admin/loginadmin"
                element={<LoginAdmin showAlert={showAlert} />}
              />

              <Route exact path="/about" element={<About />} />
              <Route
                exact
                path="/contact"
                element={<Contact showAlert={showAlert} />}
              />

              <Route
                exact
                path="/admin/services"
                element={<ServicesAdmin showAlert={showAlert} />}
              />

              <Route
                exact
                path="/get/products/:id"
                element={<GetEditProducts showAlert={showAlert} />}
              />

              <Route exact path="/admin/list" element={<ListAdmins />} />

              <Route
                exact
                path="/admin/dashadmin"
                element={<AdminDash showAlert={showAlert} />}
              />

              <Route exact path="/admin/swiper" element={<Swiper />} />
            </Routes>
            <Footer />
          </div>
        </Router>
      </Provider>
    </>
  );
}

export default App;
