import React, { useEffect } from "react";
import { docTitle } from "./docTitle";
import Aos from "aos";
import "aos/dist/aos.css";

const rprhomes = require("../components/slider/home/rprhomes.mp4");

const About = () => {
  docTitle("About - Raipur Homes");

  useEffect(() => {
    Aos.init({
      duration: 1500,
    });
  }, []);

  return (
    <>
      <main id="content" role="main">
        <div className="home-banner relative aspect-5/7 md:aspect-[16/8] 2xl:h-screen 2xl:aspect-auto bg-slate-400 w-full">
          <video
            style={{ width: "100%" }}
            className="absolute top-0 left-0 z-10 h-full object-cover w-full"
            preload="auto"
            loop={true}
            autoPlay={true}
            muted={true}
            playsInline
          >
            <source src={rprhomes} type="video/mp4" />
          </video>
        </div>
      </main>

      <div
        style={{ overflow: "hidden" }}
        data-aos="fade-up"
        className="about-d-flex"
      >
        <h3
          data-aos="fade-right"
          className="mb-3 d-flex justify-content-center border-bottom"
        >
          ABOUT OF RAIPUR HOMES
        </h3>
        <div
          data-aos="fade-left"
          className="text-align-center justify-content-center align-items-center"
        >
          <p
            style={{ textAlign: "center" }}
            className="d-flex m-auto text-align-center justify-content-center align-items-center w-75"
          >
            We are amongst the top 4 developers in India. A credo of
            uncompromising values, customer centricity, robust engineering, and
            transparent business operations has earned us many accolades from
            customers and the public at large.
          </p>
        </div>

        <div className="mt-5 d-flex align-items-center justify-content-center">
          <div
            data-aos="flip-left"
            className="card m-2 col-md-3 col-sm-6 card-body-design text-white"
          >
            <div className="card-body m-auto">
              <h1 className="card-title">170,000</h1>
              <p className="card-text">HAPPY CUSTOMERS</p>
            </div>
          </div>

          <div
            data-aos="flip-left"
            className="card m-2 col-md-3 col-sm-6 card-body-design text-white"
          >
            <div className="card-body m-auto">
              <h1 className="card-title">42,500</h1>
              <p className="card-text">HOMES DELIVERED</p>
            </div>
          </div>
          <div
            data-aos="flip-left"
            className="card m-2 col-md-3 col-sm-6 card-body-design text-white"
          >
            <div className="card-body m-auto">
              <h1 className="card-title">200</h1>
              <p className="card-text">AWARDS OF EXCELLENCE</p>
            </div>
          </div>
          <div
            data-aos="flip-left"
            className="card m-2 col-md-3 col-sm-6 card-body-design text-white"
          >
            <div className="card-body m-auto">
              <h1 className="card-title">99</h1>
              <p className="card-text">INDIAN CITIES ON ITS MAP</p>
            </div>
          </div>
        </div>
        <p data-aos="fade-left">
          Real estate is more than buying and selling a house. This
          million-dollar industry has thousands of such facts that may pique
          your interest. Whether you’re a seasoned investor or a first-time
          homebuyer, you’ll find the following 20 real estate facts interesting.
        </p>
      </div>
    </>
  );
};

export default About;
